<template>
  <div class="container">
    <ImageDrop @image-dropped="setImageSrc" @zoom-in="scaleUp" @zoom-out="scaleDown">
      <ImageMove :src="imageSrc" :scale="scale">
        <EllipseDraw v-if="ImageLoded" />
      </ImageMove>
    </ImageDrop>
  </div>
</template>

<script>
import { ref } from 'vue';
import ImageDrop from './components/ImageDrop.vue';
import ImageMove from './components/ImageMove.vue';
import EllipseDraw from './components/EllipseDraw.vue';

export default {
  data() {
    return {
      imageSrc: '',
      scale: 1,
    };
  },
  methods: {
    setImageSrc(src) {
      this.imageSrc = src;
    },
    scaleUp() {
      this.scale += 0.1;
    },
    scaleDown() {
      if (this.scale > 0.1) {
        this.scale -= 0.1;
      }
    },
  },
  components: { ImageDrop, ImageMove, EllipseDraw },
  setup() {
    const imageSrc = ref(null);
    const scale = ref(1);

    const setImageSrc = (src) => {
      imageSrc.value = src;
    };

    const scaleUp = () => {
      scale.value += 0.1;
    };

    const scaleDown = () => {
      scale.value -= 0.1;
    };

    return { imageSrc, setImageSrc, scale, scaleUp, scaleDown };
  },
};
</script>

