<template>
    <div class="container">
        <div v-if="imageLoaded" class="zoomButtons">
            <button @click="$emit('zoom-in')"><i class="fa-solid fa-magnifying-glass-plus">Zoom In</i></button> &nbsp;&nbsp;&nbsp;&nbsp;
            <button @click="$emit('zoom-out')">Zoom Out</button>
        </div>
      <div class="dropzone" @dragover.prevent @drop="onDrop">
        <p v-if="!imageLoaded">여기에 이미지를 드래그 앤 드롭하세요.</p>
        <EllipseDraw v-if="imageLoaded" :imageLoaded="imageLoaded" />
        <slot></slot>
      </div>
    </div>
</template>
  
<script>
  import EllipseDraw from './EllipseDraw.vue';
  export default {
    components: {
        EllipseDraw
    }, 
    data() {
      return {
        imageLoaded: false,
      };
    },
    methods: {
  onDrop(event) {
    event.preventDefault();
    const file = event.dataTransfer.files[0];
    if (file.type.startsWith('image/')) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const img = new Image();
        img.onload = () => {
          if (img.width > 500 || img.height > 400) {
            alert('500x400 이상의 이미지는 업로드할 수 없습니다.');
          } else {
            this.$emit('image-dropped', e.target.result);
            this.imageLoaded = true;
          }
        };
        img.src = e.target.result;
      };
      reader.readAsDataURL(file);
    } else {
      alert('이미지 파일만 업로드 가능합니다.');
    }
    },
    },

  };
</script>
  
<style scoped>
  .container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px; /*드래그 앤 드롭박스와 버튼 간 간격 설정*/
  }

  .zoomButtons {
    position: absolute;
    top: 10px;
    left: 0;
    width: 200px;
    height: 40px;
    font-size: 40px;
    display: flex;
    flex-direction: row;
  }
  .dropzone {
    width: 700px;
    height: 700px;
    border: 2px dashed #ccc;
    position: relative;
    overflow: hidden;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .ellipse {
    fill: none; 
    stroke: red; 
    stroke-width: 2; 
    stroke-dasharray: 5,5;
    border-radius: 50%;
  }
  .dropzone p {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 10;
    }
</style>
  